<template>
  <b-row class="animated fadeIn">
    <b-col cols="12">
      <b-card no-header>
        <template slot="header">ISIN: {{ isin }}</template>
        <b-form>
          <b-row>
            <b-col xl="5" lg="12" md="12" sm="12" xs="12">
              <b-form-group class="mb-0">
                <b-input-group>
                  <b-input-group-prepend>
                    <b-input-group-text>Fund ISIN</b-input-group-text>
                  </b-input-group-prepend>
                  <b-form-input
                    v-model="isinCode"
                    type="text"
                    placeholder="Enter Fund ISIN.."
                    required
                  ></b-form-input>
                  <b-input-group-append>
                    <b-input-group-text>
                      <i
                        class="fa"
                        v-b-tooltip.hover.html="'<strong>Insert a valid ISIN of Fund.</strong>'"
                        :class="classisinCode"
                      ></i>
                    </b-input-group-text>
                  </b-input-group-append>
                </b-input-group>
              </b-form-group>
            </b-col>
            <b-col xl="7" lg="12" md="12" sm="12" xs="12">
              <b-form-group class="mb-0">
                <b-input-group>
                  <b-input-group-prepend>
                    <b-input-group-text>Fund Name</b-input-group-text>
                  </b-input-group-prepend>
                  <b-form-input
                    v-model="isinName"
                    type="text"
                    placeholder="Enter Fund Name.."
                    required
                  ></b-form-input>
                  <b-input-group-append>
                    <b-input-group-text>
                      <i
                        class="fa"
                        v-b-tooltip.hover.html="'<strong>Insert a valid fund name.</strong>'"
                        :class="classisinName"
                      ></i>
                    </b-input-group-text>
                  </b-input-group-append>
                </b-input-group>
              </b-form-group>
            </b-col>
          </b-row>
          <!-- <div slot="footer">
          </div>-->
        </b-form>
        <template slot="footer">
          <b-row>
            <b-col cols="6">
              <b-button block variant="outline-primary" @click="goBack">
                <i class="cui-account-logout icons active mt-3"></i> Back
              </b-button>
            </b-col>
            <b-col cols="6">
              <b-button @click="saveFund" block class="mb-3" variant="outline-success">
                <i class="fa fa-save"></i> Save Fund
              </b-button>
            </b-col>
          </b-row>
        </template>
      </b-card>
    </b-col>
  </b-row>
</template>
<script>
import utils from "./../../shared/utilsLib.js";
export default {
  name: "Fund",
  data: () => {
    return {
      isin: "New Fund",
      isinCode: "",
      isinCodeCheck: false,
      classisinCode: "fa-close",
      isinName: "",
      isinNameCheck: false,
      classisinName: "fa-close",
      utils
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
      this.$router.replace({ path: "/funds" });
    },
    saveFund() {
      if (!this.isinCodeCheck || !this.isinNameCheck) {
        return this.$notify({
          group: "notification",
          title: "Error",
          text: "Check all fields.",
          type: "danger",
          position: "top center"
        });
      }
      let data = {
        isin: this.isinCode,
        name: this.isinName,
        idFundmorningstar: ""
      };
      this.$loading.show();
      this.$http
        .post(utils.geturl() + "/api/funds/fund", data)
        .then(function(response) {
          let data = response.data;
          console.log("save", data);
          if (data.status === true) {
            this.$notify({
              group: "notification",
              title: "New fund created.",
              text: data.data,
              type: "info",
              position: "top center"
            });
            this.$router.push("/funds");
          } else {
            this.$notify({
              group: "notification",
              title: "New fund existes.",
              type: "warning",
              text: "The fund '" + data.data.name + "' exists in database.",
              position: "top center"
            });
          }
          this.$loading.hide();
        })
        .catch(function(err) {
          if (err && err.status === 401) {
            this.$router.push("/login");
            localStorage.removeItem("user");
            this.$notify({
              group: "notification",
              title: "Login Error.",
              type: "danger",
              text: err.body.data,
              position: "top center"
            });
          } else {
            this.$notify({
              group: "notification",
              title: "Post fund error.",
              type: "danger",
              text: "Error " + err,
              position: "top center"
            });
          }
          this.$loading.hide();
        });
    }
  },
  watch: {
    isinCode: function(val) {
      let addClass = "";
      this.isinCodeCheck = false;
      if (val.trim().length > 9) {
        addClass = "check";
        this.isinCodeCheck = true;
      } else {
        addClass = "close";
      }
      this.classisinCode = "fa-" + addClass;
    },
    isinName: function(val) {
      let addClass = "";
      this.isinNameCheck = false;
      if (val.trim().length > 4) {
        addClass = "check";
        this.isinNameCheck = true;
      } else {
        addClass = "close";
      }
      this.classisinName = "fa-" + addClass;
    }
  }
};
</script>
